.project-card-container {
    display: flex;
    flex-direction: column;

    width: 400px;

    border-radius: var(--default-radius);
    position: relative;
    background-color: var(--bg-gradient);
    z-index: 0;
    box-shadow: var(--shadow-1);

    overflow: hidden;
}

.project-card-container:hover {
    cursor: pointer;
}

.project-card-container:hover img {
    transform: scale(1.05);
}

.project-card-container:hover #title {
    color: var(--primary);
}

.project-card-container::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    padding: 1px;
    background: var(--border-gradient-onyx);
    mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
    mask-composite: exclude;
    z-index: -1;
}

.project-card-container .project-img {
    background-color: aliceblue;
    width: 400px;
    height: 250px;
    border-radius: var(--default-radius);
    overflow: hidden;
    background-color: var(--bg-gradient);
}

.project-card-container .project-img img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    overflow: hidden;

    border-radius: var(--default-radius);
    transition: var(--default-transition);
    display: block;
}

.project-card-container .desc {
    padding: 10px 20px 20px;
}

.desc #timeframe {
    font-size: 14px;
    color: var(--lighter-jet);
}

.desc .timeframe-category {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.timeframe-category #category {
    font-size: 14px;
}

.desc #title {
    margin: 10px 0;
    transition: var(--default-transition);
}

.desc .project-techs {
    margin-top: 40px;
    display: flex;
    gap: 5px;

    flex-wrap: wrap;
}
